import LinkTo from 'components/atoms/LinkTo/LinkTo';
import { Case, ExpoTitle } from './SVGTitle';

export default function ExpoCardTL() {
  return (
    <div className="w-full overflow-hidden rounded-3xl bg-white shadow-md">
      <div className="blue-gradient flex h-28 items-center justify-between rounded-3xl pl-8 pr-5">
        <ExpoTitle />
        <Case />
      </div>
      <div className="my-6 px-6">
        <p className="mb-6 text-center text-sm">
          Pameran lowongan kerja, magang, beasiswa bagi para peserta dan pameran produk wirausaha dari program
          pendampingan CDA IPB.
        </p>
        <LinkTo href="/expo-hall">
          <button className="bg-button-yellow w-full rounded-full p-2 font-semibold">Ikut Expo</button>
        </LinkTo>
      </div>
    </div>
  );
}
