import LinkTo from 'components/atoms/LinkTo/LinkTo';
import { ITATitle, Toga } from './SVGTitle';

export default function ITACard() {
  return (
    <div className="w-full overflow-hidden rounded-3xl bg-white shadow-md">
      <div className="green-gradient flex h-28 items-center justify-between rounded-3xl pl-8 pr-1">
        <ITATitle />
        <Toga />
      </div>
      <div className="my-6 px-6">
        <p className="mb-6 text-center text-sm">
          Serangkaian kegiatan training yang dilaksanakan untuk mempersiapkan peserta dalam mengikuti
          rangkaian seleksi kerja pada IPB Job Fair 2023.
        </p>
        <LinkTo href="/ijf-academy">
          <button className="bg-button-yellow w-full rounded-full p-2 font-semibold">Ikut Training</button>
        </LinkTo>
      </div>
    </div>
  );
}
