import LinkTo from 'components/atoms/LinkTo/LinkTo';
import { VRGlasses, WSTitle } from './SVGTitle';

export default function WSCard() {
  return (
    <div className="w-full overflow-hidden rounded-3xl bg-white shadow-md">
      <div className="red-gradient flex h-28 items-center justify-between rounded-3xl pl-8 pr-5">
        <WSTitle />
        <VRGlasses />
      </div>
      <div className="my-6 px-6">
        <p className="mb-6 text-center text-sm">
          Presentasi industrial insight dari perusahaan yang ikut serta dalam IPB Job Fair 2023. Kegiatan
          dilakukan secara offline dan online.
        </p>
        <LinkTo href="/webinar-series">
          <button className="bg-button-yellow w-full rounded-full p-2 font-semibold">Ikut Webinar</button>
        </LinkTo>
      </div>
    </div>
  );
}
